import { faBars } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { graphql, Link, useStaticQuery } from "gatsby"
import { GatsbyImage, getImage, withArtDirection } from "gatsby-plugin-image";
import React, { useState } from "react"

const navItems = [
  ['services', '/services'],
  ['works', '/works'],
  ['career', '/career'],
  ['blog', '/blog'],
  ['request for quotation', null],
];

const MobileNav = ({
  visible,
}) => {
  const mobileNavClasses = ["bg-primary flex flex-col basis-full self-center py-7 space-y-8 justify-end"];

  if (!visible) {
    mobileNavClasses.push("hidden");
  }

  return (
    <nav className="absolute left-0 right-0 z-10">
      <div className={mobileNavClasses.join(' ')}>
        {navItems.map(([title, url]) => (
          <Link className="text-white text-center hover:text-secondary text-base" to={url}>
            {title}
          </Link>
        ))}
      </div>
    </nav>
  )
}

const DesktopNav = () => {
  return (
    <div className="hidden flex-row self-center space-x-8 justify-end lg:flex">
      {navItems.map(([title, url]) => (
        <Link className="font-bold text-primary hover:text-secondary text-xl" to={url}>
          {title}
        </Link>
      ))}
    </div>
  )
}

const Navbar = ({ logoOnly }) => {
  const { strapiGlobal } = useStaticQuery(graphql`
    query {
      strapiGlobal {
        logo {
          localFile {
            childImageSharp {
              gatsbyImageData(
                width: 53
                placeholder: TRACED_SVG
              )
            }
          }
        }
        logoFull {
          localFile {
            childImageSharp {
              gatsbyImageData(placeholder: TRACED_SVG)
            }
          }
        }
        desktopLogo: logo {
          localFile {
            childImageSharp {
              gatsbyImageData(
                width: 83
                placeholder: TRACED_SVG
              )
            }
          }
        }
      }
    }
  `)

  const { desktopLogo, logo, logoFull } = strapiGlobal;
  const logoOnlyImages = withArtDirection(getImage(desktopLogo.localFile), [
    {
      media: "(max-width: 767px)",
      image: getImage(logo.localFile),
    },
  ])
  const logoImg = logoOnly ? logoOnlyImages : logoFull.localFile;

  const [isMobileNavVisible, setIsMobileNavVisible] = useState(false);

  return (
    <header className="relative z-10">
      <nav className="container flex flex-wrap flex-row items-center justify-between py-3">
        <Link to="/" className="text-xl font-medium">
          <GatsbyImage className="w-[53px] lg:w-[83px]" image={getImage(logoImg)} alt="Logo" />
        </Link>
        <button onClick={() => setIsMobileNavVisible(!isMobileNavVisible)}>
          <FontAwesomeIcon className="text-xl visible lg:invisible" icon={faBars} />
        </button>
        <DesktopNav />
      </nav>
      <MobileNav visible={isMobileNavVisible} />
    </header>
  )
}

export default Navbar
